.App {
  text-align: center;
}

.login-form {
  width: 400px;
    margin: 100px auto;
}
  .login-form-form {
    margin-bottom: 15px;
      background: #f7f7f7;
      box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
      padding: 30px;
  }
  .form-control {
      min-height: 38px;
      border-radius: 2px;
  }

  .Stick-Right {
    position: fixed;
    z-index: 100;
    right: 0;
    top: 0;
    background-color: rgb(112, 117, 128);
    color: rgb(255, 255, 255);
    padding: 20px;
    font-size: 20px;
    border-radius: 10px;
  } 

